@media (min-width: 1200px) {
  .home-login-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
    background: whitesmoke;
    margin: none;
  }

  .form-login-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
    background: white;
    height: 500px;
    width: 28%;
    border: 2px solid rgb(55, 144, 255);
    border-radius: 20px;
    padding-bottom: 15px;
  }

  .input-1-login-page {
    border: 2px solid rgb(55, 144, 255);
    margin-top: 50px;
    width: 90%;
    height: 50px;
    border-radius: 50px;
    outline: none;
    padding-left: 20px;
  }

  .input-2-login-page {
    width: 90%;
    height: 40px;
    outline: none;
    margin-left: 20px;
    border: none;
  }

  .text-1-login-page {
    color: rgb(55, 144, 255);
    width: 100%;
    display: flex;
    justify-content: center;
    font-size: 30px;
    margin-top: 40px;
  }

  .text-2-login-page {
    margin-top: 30px;
    width: 90%;
    display: flex;
    color: gray;
    font-size: 15px;
    justify-content: start;
  }

  .btn-1-login-page {
    width: 50%;
    height: 50px;
    border-radius: 50px;
    background: rgb(55, 144, 255);
    border: none;
    margin-top: 50px;
    font-size: 17px;
    cursor: pointer;
    color: white;
  }

  .btn-1-login-page:hover {
    opacity: 80%;
  }

  .btn-1-login-page:active {
    width: 50%;
    height: 50px;
    border-radius: 50px;
    border: 2px solid rgb(55, 144, 255);
    margin-top: 50px;
    font-size: 17px;
    cursor: pointer;
    color: rgb(55, 144, 255);
  }

  .text-3-login-page {
    margin-top: 40px;
    color: gray;
  }

  .div-1-login-page {
    display: flex;
    justify-content: space-between;
    width: 95%;
    align-items: center;
    border: 2px solid rgb(55, 144, 255);
    margin-top: 50px;
    height: 50px;
    border-radius: 50px;
    outline: none;
    border: 2px solid rgb(55, 144, 255);
  }

  .login-page-img-1 {
    margin-right: 20px;
    cursor: pointer;
    width: 20px;
  }

  .login-page-img-1:hover {
    opacity: 80%;
  }

  .login-page-img-1:active {
    opacity: 60%;
  }

  .login-page-img-2 {
    margin-right: 20px;
    display: none;
    cursor: pointer;
    width: 20px;
  }

  .login-page-img-2:hover {
    opacity: 80%;
  }

  .login-page-img-2:active {
    opacity: 60%;
  }
}

