@media (min-width: 1200px) {
  .aboutus {
    display: flex;
    width: 100%;
    justify-content: start;
    align-items: center;
    flex-direction: column;
  }

  .aboutus-text-1 {
    font-size: 30px;
    color: rgb(8, 7, 8);
    font-family: Verdana, Times, serif;
  }

  .aboutus-text-1 span {
    font-family: Verdana, Times, serif;
    color: rgb(55, 144, 255);
  }

  .aboutus-section-1 {
    width: 70%;
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .aboutus-section-1 a {
    text-align: center;
  }

  .aboutus-section-1 h3 {
    margin-top: 50px;
    font-family: Verdana, Times, serif;
    color: grey;
  }
}

